import React from 'react';
import MenuList from './layout/MenuList';
// import MenuForm from './layout/MenuForm';
import { getMenus } from '../../api/menuApi';


class Menu extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: []
    }
  }

  componentDidMount() {
    getMenus()
    .then(
      (result) => {
      console.log(result);
      this.setState(
        {
          isLoaded: true,
          items: result
        });
      },
      (error) => {
        this.setState({
          isLoaded: false,
          error
        });
      }
    );
  }

  render() {
    const { error, isLoaded, items} = this.state;
    if(error) {
      return <div>Error: {error.message}</div>
    }
    else if(!isLoaded) {
      return <div>Loading...</div>
    }
    else {
      return (
      <MenuList items={items}/>
      );
    }
  }
}

export default Menu;

